import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Checkbox, Row, Col, Typography, message } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { login } from '../api';

const { Title } = Typography;

const Login = () => {
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        document.title = "Đăng Nhập"; 
    }, []);

    const onFinish = async (values) => {
        setLoading(true);

        try {
            const response =  await login(values)                

            if (response.data.success) {
                message.success('Đăng nhập thành công!');
                window.location.href = '/';
            } else {
                message.error(`Đăng nhập thất bại: ${response.data.data}`);
            }
        } catch (error) {
            message.error(`Có lỗi xảy ra: ${error.message}`);
        }

        setLoading(false);
    };

    const handleForgotPassword = () => {
        message.info('Hãy truy cập vào trang quên mật khẩu của chúng tôi.'); 
        // Hoặc chuyển hướng người dùng đến trang quên mật khẩu
    };

    return (
        <Row justify="center" style={{ marginTop: '50px' }}>
            <Col xs={24} sm={20} md={16} lg={8} xl={6}>
                <Title level={2} style={{ textAlign: 'center', marginBottom: '20px' }}>
                    Đăng Nhập
                </Title>
                <Form
                    name="login"
                    onFinish={onFinish}
                    layout="vertical"
                    initialValues={{ remember: true }}
                >
                    <Form.Item
                        name="username"
                        rules={[{ required: true, message: 'Vui lòng nhập Email hoặc số điện thoại!' }]}
                    >
                        <Input 
                            prefix={<UserOutlined />} 
                            placeholder="Email hoặc số điện thoại " 
                            size="large" 
                        />
                    </Form.Item>
                    <Form.Item
                        name="password"
                        rules={[{ required: true, message: 'Vui lòng nhập mật khẩu!' }]}
                    >
                        <Input.Password 
                            prefix={<LockOutlined />} 
                            placeholder="Mật khẩu" 
                            size="large" 
                        />
                    </Form.Item>
                    <Form.Item>
                        <Row justify="space-between">
                            <Col>
                                <Form.Item name="remember" valuePropName="checked" noStyle>
                                    <Checkbox>Ghi nhớ tài khoản</Checkbox>
                                </Form.Item>
                            </Col>
                            <Col>
                                <Button type="link" onClick={handleForgotPassword}>
                                    Quên mật khẩu?
                                </Button>
                            </Col>
                        </Row>
                    </Form.Item>
                    <Form.Item>
                        <Button 
                            type="primary" 
                            htmlType="submit" 
                            block 
                            size="large" 
                            loading={loading}
                        >
                            Đăng nhập
                        </Button>
                    </Form.Item>
                </Form>
            </Col>
        </Row>
    );
};

export default Login;
